// polyfills
import 'core-js/fn/array/from';
import './modules/polyfills';

// detect mobile devices
import './modules/mobileDetect';

// external svg support for old browsers
import 'svgxuse';

// jQuery
import jQuery from 'jquery';
window.$ = window.jQuery = jQuery;

// modules
import { overlay } from './modules/overlay';
import { scrollTo } from './modules/scrollTo';
import { popup } from './modules/popup';
import { accordion } from './modules/accordion';
import { clients } from './modules/clients';
import { reviews } from './modules/reviews';
import { telMask } from './modules/telMask';
import { nav } from './modules/nav';
import { images } from './modules/images';
import { gallery } from './modules/gallery';

window.overlay = overlay;
window.popup = popup;
window.initModules = () => {
    scrollTo();
    accordion();
    clients();
    reviews();
    telMask();
    nav();
    gallery();
    images();
    popup();

    require('./modules/fancybox')();
};

// init modules on page load
if (document.readyState === 'complete') {
    if (!document.body.classList.contains('preload')) {
        initModules();
    } else {
        preload(initModules);
    }
} else if (document.readyState === 'interactive' && !document.body.classList.contains('preload')) {
    initModules();
} else {
    document.addEventListener('DOMContentLoaded', () => {
        if (!document.body.classList.contains('preload')) {
            initModules();
        }
    });

    window.addEventListener('load', () => {
        if (document.body.classList.contains('preload')) {
            preload(initModules);
        }
    });
}