import Swiper from 'swiper';

const moduleName = 'images';

export function images() {

    Array.from(document.querySelectorAll('.js-images')).forEach((self) => {
        if (self.dataset[moduleName + 'Init'] === 'true') return;
        self.dataset[moduleName + 'Init'] = true;

        const holder = self;
        const slider = holder.querySelector('.js-images-slider');
        const pagination = document.createElement('div');

        pagination.className = 'images__thumbs';

        holder.insertBefore(pagination, slider);

        const swiper = new Swiper(slider, {
            init: false,
            simulateTouch: false,
            effect: 'fade',
            pagination: {
                el: pagination,
                clickable: true,
                bulletClass: 'images__thumb',
                bulletActiveClass: '_active',
                renderBullet: (index, className) => {
                    let url = swiper.slides[index].querySelector('img').getAttribute('src');

                    return '<div class="' + className + '" style="background-image: url(' + url + ');"></div>';
                }
            }
        });

        swiper.init();
    });

}