import Swiper from 'swiper';

const moduleName = 'reviews';

export function reviews() {

    Array.from(document.querySelectorAll('.js-reviews')).forEach((self) => {
        if (self.dataset[moduleName + 'Init'] === 'true') return;
        self.dataset[moduleName + 'Init'] = true;

        const holder = self;
        const slider = holder.querySelector('.js-reviews-slider');
        const prevButton = holder.querySelector('.js-reviews-button-prev');
        const nextButton = holder.querySelector('.js-reviews-button-next');
        const pagination = holder.querySelector('.js-reviews-pagination');
        const swiper = new Swiper(slider, {
            simulateTouch: false,
            slidesPerView: 2,
            slidesPerGroup: 2,
            navigation: {
                prevEl: prevButton,
                nextEl: nextButton,
                disabledClass: '_disabled'
            },
            pagination: {
                el: pagination,
                clickable: true,
                bulletClass: 'pagination__bullet',
                bulletActiveClass: '_active'
            },
            breakpoints: {
                567: {
                    slidesPerView: 1,
                    slidesPerGroup: 1
                }
            }
        });
    });

}