import Swiper from 'swiper';

const moduleName = 'clients';

export function clients() {

    Array.from(document.querySelectorAll('.js-clients')).forEach((self) => {
        if (self.dataset[moduleName + 'Init'] === 'true') return;
        self.dataset[moduleName + 'Init'] = true;

        const holder = self;
        const slider = holder.querySelector('.js-clients-slider');
        const prevButton = holder.querySelector('.js-clients-button-prev');
        const nextButton = holder.querySelector('.js-clients-button-next');
        const pagination = holder.querySelector('.js-clients-pagination');
        const swiper = new Swiper(slider, {
            simulateTouch: false,
            slidesPerView: 7,
            slidesPerGroup: 7,
            spaceBetween: 40,
            navigation: {
                prevEl: prevButton,
                nextEl: nextButton,
                disabledClass: '_disabled'
            },
            pagination: {
                el: pagination,
                clickable: true,
                bulletClass: 'pagination__bullet',
                bulletActiveClass: '_active'
            },
            breakpoints: {
                439: {
                    slidesPerView: 2,
                    slidesPerGroup: 2,
                    spaceBetween: 20
                },
                579: {
                    slidesPerView: 3,
                    slidesPerGroup: 3,
                    spaceBetween: 20
                },
                719: {
                    slidesPerView: 4,
                    slidesPerGroup: 4,
                    spaceBetween: 20
                },
                1023: {
                    slidesPerView: 5,
                    slidesPerGroup: 5,
                    spaceBetween: 20
                },
                1320: {
                    slidesPerView: 6,
                    slidesPerGroup: 6
                }
            }
        });
    });

}