const moduleName = 'nav';

export function nav() {

    Array.from(document.querySelectorAll('.js-nav')).forEach((self) => {
        if (self.dataset[moduleName + 'Init'] === 'true') return;
        self.dataset[moduleName + 'Init'] = true;

        const BREAKPOINT = 768;

        const nav = self;
        const openButton = nav.querySelector('.js-nav-button-open');
        const closeButton = nav.querySelector('.js-nav-button-close');

        openButton.addEventListener('click', () => {
            nav.classList.add('_open');
            overlay().show();
        });

        closeButton.addEventListener('click', hideMenu);

        document.addEventListener('click', (event) => {
            if (!event.target.closest('.js-nav') && nav.classList.contains('_open')) hideMenu();
        });

        window.addEventListener('resize', () => {
            if (document.body.clientWidth >= BREAKPOINT && nav.classList.contains('_open')) hideMenu();
        });



        function hideMenu() {
            nav.classList.remove('_open');
            overlay().hide();
        }
    });

}