const moduleName = 'accordion';

export function accordion() {

    Array.from(document.querySelectorAll('.js-accordion')).forEach((self) => {
        if (self.dataset[moduleName + 'Init'] === 'true') return;
        self.dataset[moduleName + 'Init'] = true;

        const SPEED = 300;

        const accordion = self;
        const accordionToggle = accordion.querySelector('.js-accordion-toggle');
        const accordionOuter = accordion.querySelector('.js-accordion-outer');
        const accordionInner = accordion.querySelector('.js-accordion-inner');

        accordionToggle.addEventListener('click', () => {
            if (accordion.classList.contains('_open')) {
                slideUp();
            } else {
                slideDown();
            }
        });



        function slideUp() {
            accordionOuter.style.height = accordionOuter.offsetHeight + 'px';

            setTimeout(() => {
                accordionOuter.style.height = 0;
                accordion.classList.remove('_open');

                setTimeout(() => {
                    accordionOuter.style.height = '';
                }, SPEED);
            }, 4);
        }

        function slideDown() {
            accordionOuter.style.height = 0;

            setTimeout(() => {
                accordion.classList.add('_open');
                accordionOuter.style.height = accordionInner.offsetHeight + 'px';

                setTimeout(() => {
                    accordionOuter.style.height = '';
                }, SPEED);
            }, 4);
        }
    });

}